import { SubmitHandler, UseFormReturn } from "react-hook-form";

export interface IUser {
  username?: string;
  password?: string;
}

export enum Warning {
  username = " ",
  password = "",
  name = "",
  surname = "",
  city = "",
  tel = " ",
}

export interface CustomError {
  data: string;
  status: number;
}

export interface IContact {
  id?: string;
  name: string;
  surname: string;
  tel: string;
  city: string;
  hidden?: boolean;
}

export interface IValidationRules {
  regExp: RegExp;
  maxLength: number;
  minLength: number;
}

export interface IPopupState {
  active: boolean;
  data?: IContact;
}

export enum SelectType {
  ascendingAlphabet = "  ",
  descendingAlphabet = "  ",
  name = " ",
  surname = " ",
  city = "  ",
  tel = "  ",
}

export type refreshJWTSliceState = number | null;

export type searchType = "name" | "surname" | "city" | "tel";

export interface ISearch {
  type: searchType;
  data: string;
  fetchData: IContact[];
}
export type FormSubmitHandler = SubmitHandler<IContact | IUser>;
export type FormMethods = UseFormReturn<IContact | IUser>;
